<template>
  <header>
    <div class="header">Weather App In Vue</div>
  </header>
  <div
    id="app"
    :class="[
      typeof weather.main != 'undefined' && weather.main.temp > 16
        ? 'warm'
        : '',
      typeof weather.main != 'undefined' && weather.main.temp < 16
        ? 'cold'
        : '',
        typeof weather.main != 'undefined' && weather.main.temp > 16 && weather.weather[0].main == 'Rain' ? 'rain' : '',
    ]"
  >
    <main>
      <div class="search-box">
        <input
          type="text"
          class="search-bar"
          placeholder="Search any City name..."
          v-model="query"
          @keypress="fetchWeather"
        />
        {{ query }}
      </div>
      <div class="weather-wrap" v-if="typeof weather.main != 'undefined'">
        <div class="location-box">
          <div class="location">
            {{ weather.name }}, {{ weather.sys.country }}
          </div>
          <div class="date">{{ dateBuilder() }}</div>
        </div>
        <div class="weather-box">
          <div class="temp">{{ Math.round(weather.main.temp) }}°c</div>
          <div class="weather">
            Weather:
            {{ weather.weather[0].main }}
          </div>
          <div class="temp-feel">
            Feel Like: {{ Math.round(weather.main.temp) }}°c
          </div>
          <div class="temp-min">
            Min Temp: {{ Math.round(weather.main.temp_min) }}°c
          </div>
          <div class="temp-max">
            Max Temp: {{ Math.round(weather.main.temp_max) }}°c
          </div>

          <div class="humidity">Humidity: {{ weather.main.humidity }} %</div>
          <div class="wind-speed">
            Wind Speed: {{ weather.wind.speed }} km/hr
          </div>
          <div class="visibility">Visibility: {{ weather.visibility }} m</div>
          <div class="pressure">Pressure: {{ weather.main.pressure }} Pa</div>
        </div>
      </div>
    </main>
  </div>
  <footer class="footer">
    <p>Copyright &copy; - 2021 | Weather App | by <a href="https://vivek.webtouchsolution.com"> Vivek</a></p>
  </footer>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      api_key: "ed2c019cfef3d5f5e409b8e63cef8569",
      url_base: "https://api.openweathermap.org/data/2.5/",
      query: "",
      weather: {},
    };
  },
  methods: {
    fetchWeather(e) {
      if (e.key == "Enter") {
        fetch(
          `${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`
        )
          .then((res) => {
            return res.json();
          })
          .then(this.setResults);
      }
    },
    setResults(results) {
      this.weather = results;
    },
    dateBuilder() {
      let d = new Date();
      let months = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
      let days = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thrusday",
        "Friday",
        "Saturday",
      ];

      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();

      return `${day} ${date} ${month} ${year}`;
    },
  },
};
</script>

